@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --main-text-color: #444444;
  --accent-text-color: #000000;
  --additional-text-color: #222222;
  --action-color: #ffffff;
  --main-bg-color: #68b738;
  --accent-bg-color: #f2f2f2;
}

.page {
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  color: var(--main-text-color);
}

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--action-color);
  background-color: var(--main-bg-color);
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
}

.field {
  border: none;
}

.field__contacts-legend {
  margin-bottom: 35px;
}

.field__extra-legend {
  margin-bottom: 34px;
}

.legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--inner-title-size);
  line-height: 26px;
  color: var(--accent-text-color);
  display: block;
}

.label {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
}

.input::placeholder {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
}

.title {
  --title-size: 36px;
  --inner-title-size: 24px;
  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--accent-text-color);
}

.description {
  --description-size: 14px;
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 18px;
}

.offer {
  height: 100%;
  background: linear-gradient(rgba(104, 183, 56, 0.85) 0% 63%, #ffffff 63% 100%), url("../img/main_bg.jpg") no-repeat;
}

.offer__chapter {
  margin-bottom: 20px;
}

.offer__title {
  font-size: var(--title-size);
  line-height: 36px;
  color: var(--action-color);
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
}

.offer__text {
  font-family: var(--accent-font);
  line-height: 14px;
  color: var(--action-color);
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  margin-bottom: 49px;
}

.offer__button {
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  margin: 0;
}

.options__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.options__item {
  padding: 21px 33px 18px 19px;
  background-color: var(--accent-bg-color);
  margin-bottom: 20px;
}

.options__title {
  font-size: var(--inner-title-size);
  line-height: 37px;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  margin-bottom: 29px;
}

.options__description {
  padding: 0;
  margin: 0;
  margin-bottom: 38px;
}

.options__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--additional-text-color);
  text-transform: uppercase;
  text-decoration: none;
}

.process__chapter {
  margin-bottom: 49px;
}

.process__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
}

.process__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.process__item:not(:last-child) {
  margin-bottom: 24px;
}

.process__description {
  margin: 0;
}

.example {
  background-color: #eaeaea;
}

.example__chapter {
  padding: 25px 0 40px;
}

.example__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
}

.example__description {
  margin: 0;
  margin-bottom: 20px;
}

.example__list {
  margin: 0;
}

.stats__item {
  margin-bottom: 20px;
}

.stats__item:last-child {
  margin-bottom: 40px;
}

.stats__description {
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  margin: 0;
}

.stats__number {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: var(--accent-text-color);
  margin-bottom: 8px;
}

.location {
  background-color: var(--accent-bg-color);
  padding: 26px 0;
}
.location--main_background {
  background-color: var(--action-color);
}

.location__suggestion {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}

.address {
  line-height: 20px;
  font-style: normal;
}

.questionnaire__chapter {
  margin-bottom: 32px;
}

.questionnaire__title {
  font-size: var(--title-size);
  line-height: 36px;
  margin-bottom: 37px;
}

.questionnaire__description {
  font-family: var(--accent-font);
  color: var(--accent-text-color);
  text-transform: uppercase;
  margin-bottom: 39px;
}

.questionnaire__footnote {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 32px;
}

.form__indent {
  padding: 0;
  margin: 0;
}

.form__general-info {
  margin-bottom: 50px;
}

.form__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.form__choice {
  margin-bottom: 45px;
}

.form__contacts {
  margin-bottom: 23px;
}

.form__extra {
  margin-bottom: 34px;
}

.form__button {
  padding: 0;
  margin: 0;
  margin-bottom: 21px;
}

.general-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.general-list__label {
  margin-right: 13px;
}

.choice-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.choice-list__label {
  color: var(--additional-text-color);
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 14px;
}

.extra-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.extra-list__label {
  color: var(--accent-text-color);
}

.comment__chapter {
  margin-bottom: 27px;
}

.comment__legend {
  margin-bottom: 38px;
}

.main-inner__title {
  font-size: var(--title-size);
  line-height: 36px;
  padding: 0;
  margin: 0;
}

.catalog__table {
  margin-bottom: 50px;
}

.table__line {
  margin-bottom: 33px;
}

.table__line:last-child {
  margin-top: 50px;
}

.table__title {
  padding: 0;
  margin: 0;
  margin-bottom: 14px;
}

.table__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--additional-text-color);
  text-decoration: none;
  text-transform: uppercase;
}

.table__cell {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 2px;
}

.add-goods__table {
  margin-bottom: 54px;
}

.add-goods__title {
  font-size: var(--inner-title-size);
  line-height: 24px;
  padding: 0;
  margin: 0;
  margin-bottom: 49px;
}

.add-table__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--additional-text-color);
  text-transform: uppercase;
  margin-bottom: 13px;
}

.add-table__cell {
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 14px;
  margin-bottom: 15px;
}

.present {
  background: rgba(104, 183, 56, 0.85) url("../img/main_bg.jpg") no-repeat center;
}

.present__chapter {
  margin-bottom: 35px;
}

.present__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--action-color);
  padding: 0;
  margin: 0;
}